<template>
  <div class="wrap" >
      <div class="table-box" >
          <table class="table" >
              <caption>商品信息批量修改 <span class="tip">注：返回商品列表时请刷新页面查看最新商品数据！</span></caption>
              <thead>
                  <tr>
                      <th>产品标题 </th>
                      <th width="150">包装重量 <span class="edit" @click="batch('weight')">修改</span> <i class="iconfont icon-help" title="准确填写产品包装重量，避免因填写错误而造成的运费损失和交易性降低。重量的修改影响运费的计算,如最新修改的运费未生效时，生成订单对应的运费还是原来修改前的运费。"></i></th>
                      <th width="150">包装尺寸 <span class="edit" @click="batch('size')">修改</span><i class="iconfont icon-help" title="准确填写产品包装尺寸，避免因填写错误而造成的运费损失和交易性降低。尺寸的修改影响运费的计算，如最新修改的运费未生效时，生成订单对应的运费还是原来修改前的运费。"></i></th>
                      <th width="250">运费模板 <span class="edit" @click="batch('freight')">修改</span><i class="iconfont icon-help" title="运费的设置和更改是在每天凌晨生效，当关联了新的运费模板而运费未生效时，生成订单对应的运费还是原来修改前的运费，运费生效后生成的订单才是最新的运费。"></i></th>
                      <th width="180">原价|售价<span class="edit" @click="batch('price')">修改</span></th>
                      <th width="180">SKU售价区间(USD)</th>
                  </tr>
              </thead>
              <tbody v-loading="loading">
                  <tr v-for="item in goodsList " :key="item.goods_id">
                      <td>
                          <div class="goods-info">
                              <div class="img"><img :src="item.cover_image" ></div>
                              <div class="name">{{item.goods_name}}</div>
                          </div>
                      </td>
                      <td>{{item.goods_weight.toFixed(3)}}千克</td>
                      <td>
                          <div class="size">
                              <div><span>长:</span>{{item.package_length}}厘米</div>
                              <div><span>宽:</span>{{item.package_width}}厘米</div>
                              <div><span>高:</span>{{item.package_height}}厘米</div>
                          </div>
                      </td>
                      <td>
                          <div class="freight">{{item.template_title}}(发货地:{{item.country}})</div>
                      </td>
                      <td>
                          <div class="price"><i class="iconfont icon-yuan"></i>{{item.market_price?parseFloat(item.market_price).toFixed(2):''}}</div>
                          <div class="price"><i class="iconfont icon-shou"></i>{{item.shop_price?parseFloat(item.shop_price).toFixed(2):''}}</div>
                      </td>
                      <td>
                          <div class="price">
                               {{item.min_price?parseFloat(item.min_price).toFixed(2):""}}- {{item.max_price?parseFloat(item.max_price).toFixed(2):""}}
                          </div>
                      </td>
                  </tr>
              </tbody>
          </table>
          <!-- 批量修改 -->
          <el-dialog :title="'批量修改'+getFormTitle" :visible.sync="dialogForm" size="tiny">
            <el-form :model="batchForm" label-width="100px" ref="batchForm">
                <div class="form-weight" v-if="batchType=='weight'">
                    <div class="item">
                        <el-radio class="radio" v-model="batchForm.type" label="1">批量修改为</el-radio>
                        <el-input v-model="batchForm.weight_1" size="small" @blur="formatNumber('weight_1',3)"></el-input>千克
                    </div>
                    <div class="item">
                        <el-radio class="radio" v-model="batchForm.type" label="2">按重量增加</el-radio>
                        <el-input v-model="batchForm.weight_2" size="small" @blur="formatNumber('weight_2',3)"></el-input>千克
                        <div class="tip" style="margin-left:100px;margin-top:15px;">提示：如果减少，可以输入负数</div>
                    </div>
                </div>
                <div class="form-size" v-if="batchType=='size'">
                    <el-form-item label="长">
                        <el-input v-model="batchForm.packageLength" size="small" @blur="formatNumber('packageLength',0)"></el-input>厘米
                    </el-form-item>
                    <el-form-item label="宽">
                        <el-input v-model="batchForm.packageWidth" size="small" @blur="formatNumber('packageWidth',0)"></el-input>厘米
                    </el-form-item>
                    <el-form-item label="高">
                        <el-input v-model="batchForm.packageHeight" size="small" @blur="formatNumber('packageHeight',0)"></el-input>厘米
                    </el-form-item>
                </div>
                <div class="form-freight" v-if="batchType=='freight'">
                    <el-form-item label="运费模板">
                        <el-select v-model="batchForm.freightTemplateId" clearable filterable size="small">
                            <el-option :label="item.templateTitle+'('+item.countryName+')'" :value="item.id" v-for="item in freightList" :key="item.id"></el-option>              
                        </el-select>
                    </el-form-item>
                </div>
                <div class="form-price" v-if="batchType=='price'">
                    <el-form-item label="原价价格增加">
                        <el-input v-model="batchForm.marketPrice" size="small" @blur="formatNumber('marketPrice',2)"></el-input>USD
                    </el-form-item>
                    <el-form-item label="基础价格增加">
                        <el-input v-model="batchForm.price" size="small" @blur="formatNumber('price',2)"></el-input>USD
                        <div class="tip">提示：如果减少，可以输入负数</div>
                    </el-form-item>
                    
                    
                </div>

            </el-form>
            <div slot="footer" class="dialog-footer" >
                <el-button @click="dialogForm = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </div>
        </el-dialog>
      </div>
  </div>
</template>

<script>
export default {
  data(){
      return{
          goodsIds:[],
          goodsList:[],
          freightList:[],
          loading:false,
          dialogForm:false,
          batchType:'',
          batchForm:{
              begin:'',
              end:'',
              target:'',
              replace:'',
              freightTemplateId:'',
              type:'',
              weight_1:'',
              weight_2:'',
              packageLength:'',
              packageWidth:'',
              packageHeight:'',
              price:'',
              marketPrice:''
          }
      }
  },
  computed:{
      getFormTitle(){
          let str='';
          switch(this.batchType){
              case 'name':
                str='标题' 
              break;
              case 'weight':
                str='包装重量' 
              break;
              case 'size':
                str='包装尺寸' 
              break;
              case 'freight':
                str='运费模板' 
              break;
              case 'price':
                str='价格' 
              break;
          }
          return str;
      }
  },
  methods:{
      //获取商品信息
    getGoodsList(){
      
        this.loading=true;
        this.axios.post("/proxy/fv/supplier/goods.php?act=listGoodsInfo", {body: {goods_id:this.goodsIds.join(',')}}).then(response => {
            this.loading = false;
            let data = response.data;
            if (data.head.code == "10000") {
                this.goodsList = data.body.rows;          
            }
        })
    },
    getfreightList(){
        let searchForm={
                pager: {
                    page: 1,
                    pageSize: 1000,
                    isSort: true,
                    order: "desc",
                    sort: "create_time"
                },
                
            }
        this.axios.post("/proxy/freight/api/template/list", {body: searchForm}).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {
                let arr=JSON.parse(JSON.stringify(data.body.rows));
                let tmp=[];
                for(let i in arr){
                    if(arr[i].supplierId!='0'){
                        tmp.push(arr[i]);     
                    }
                }
                this.freightList=tmp;
            }
        });
    },
    //包装 长宽高 数字校验
    formatNumber(val,decimal) {
      if (!isNaN(parseFloat(this.batchForm[val]))) {
        if(!(val =='price'||val =='weight_2'||val =='marketPrice')){
          this.batchForm[val] = Math.abs(parseFloat(this.batchForm[val]).toFixed(decimal));
        }else{
          this.batchForm[val] = parseFloat(this.batchForm[val]).toFixed(decimal);
        }
        if((val=='weight_1'||val=='weight_2')&&parseFloat(this.batchForm[val])==0){
          this.batchForm[val]='';
        }
      } else {
        this.batchForm[val] = "";
      }
      
    },
    batch(type){
        this.batchForm={
              begin:'',
              end:'',
              target:'',
              replace:'',
              freightTemplateId:'',
              type:'',
              weight_1:'',
              weight_2:'',
              packageLength:'',
              packageWidth:'',
              packageHeight:'',
              price:'',
              marketPrice:''
          },
        this.batchType=type;
        this.dialogForm=true;
        
    },
    //提交表单
    submit(){
        let url='';
        let params=JSON.parse(JSON.stringify(this.batchForm));
        params.goodsIds=this.goodsIds;
        switch(this.batchType){
            case 'freight':
                url='/proxy/goods/api/goods/batch/template';
            break;
            case 'size':
                url='/proxy/goods/api/goods/batch/size';
                params.packageLength=parseFloat(params.packageLength)*10;
                params.packageWidth=parseFloat(params.packageWidth)*10;
                params.packageHeight=parseFloat(params.packageHeight)*10;
            break;
            case 'weight':
                url='/proxy/goods/api/goods/batch/weight';
                if(params.type==1){
                    params.weight= parseFloat(params.weight_1)*1000;
                }else{
                    params.weight= parseFloat(params.weight_2)*1000;
                }
            break;
            case 'price':
                url='/proxy/goods/api/goods/batch/price';
                params.price=parseFloat(params.price)*100;
                params.marketPrice=parseFloat(params.marketPrice)*100;
            break;
        }
        
        this.axios.post(url,{body:params}).then(res=>{
            let data=res.data;
            if(data.head.code=='10000'){
                this.$message({
                    message: '修改成功',
                    type: 'success'
                })
                this.dialogForm=false;
                this.getGoodsList();
                if(data.body&&data.body.errorMsg&&data.body.errorMsg.length>0){
                    let errorMsg=data.body.errorMsg;
                    const h = this.$createElement;
                    let arr=errorMsg.map(item=>{
                        return h('p', null, item)
                    })
                    this.$msgbox({
                        title: '提示',
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        message: h('div', null,arr )
                    });
                }
                
            }
        })
    }

  },

  created(){
      this.goodsIds=JSON.parse(sessionStorage.getItem('batch')).gids;
      this.getGoodsList();
      this.getfreightList();
   
  }
}
</script>

<style lang="less" >
table,td,tr,thead,tbody,th{
    margin:0;
    padding: 0;
}
.goods-main{
    height: 100%;
}
.wrap{
    height: 100%;
    overflow: hidden;
}
.table-box{
   height: 100%;
   overflow-y: auto;
}
.table{

    border-collapse:collapse;
     width: 95%;
    margin: 30px auto;
    border: 1px solid #ccc;
    caption {
        text-align: left;
        margin-bottom: 20px;
        color: #222;
        font-size: 16px;
        .tip{
            color:#FA443F;
            margin-left: 25px;
            font-size: 14px;
        }
    }
    .edit{
        color: #20A0FF;
        text-decoration: underline;
        cursor: pointer;
        margin-left: 4px;
    }
    td,th{
        text-align: left;
        padding: 10px;
        line-height: 1.42857143;
        vertical-align: top;
        border-bottom: 1px solid #ccc;
        color: #666;
        font-weight: normal;
        font-size: 14px;
        .icon-yuan{
            color: #666;
            font-size: 22px;
            display: inline-block;
            vertical-align: middle;           
        }
        .icon-shou{
            color: #20A0FF;
            font-size: 22px;
            display: inline-block;
            vertical-align: middle;
        }
        .icon-help{
            margin-left: 6px;
            vertical-align: middle;
            display: inline-block;
            color: #BBD99A;
            cursor: pointer;
        }
        .size{
            color: #000;
        }
        .goods-info{
            padding-left: 100px;
            position: relative;
            min-height: 90px;
            .img{
                position: absolute;
                top: 0px;
                left: 0px;
                border: 1px solid #ccc;
                border-radius: 8px;
                padding: 5px;
                &::after{
                    width: 0;
                    display: inline-block;
                    height: 100%;
                    content: "";
                    vertical-align: middle;
                }
                img{
                    width: 80px;
                    height: 80px;
                    border-radius: 8px;
                    display: inline-block;
                    vertical-align: middle;
                    
                }
            }
        }
    }
}
.el-input--small{
    width: 150px;
   
    margin-right: 5px;
}
.form-weight{
    .item{
        margin-bottom: 20px;
        .el-input--small{
            margin-left: 10px;      
        }
    }
}
.el-message-box__message p {
    margin: 0;
    line-height: 1;
    margin-bottom: 15px;
}
.el-message-box{
    width: 750px;
}
.el-message-box__message{
    max-height: 400px;
    overflow-y: auto;
}
.dialog-footer{
    text-align: center;
}
.form-price,.form-weight{
    .tip{
        color: #ccc;
    }
}
.el-form-item {
    margin-bottom: 0px;
}
</style>





